export default {
  name: 'Breadcrumbs',
  data () {
    return {
      breadcrumbs: []
    }
  },
  mounted () {
    this.breadcrumbs = this.$store.getters.getBreadcrumbs
  },
  computed: {
    getBreadcrumbs () {
      return this.$store.getters.getBreadcrumbs
    }
  },
  watch: {
    getBreadcrumbs (newVal, oldVal) {
      this.breadcrumbs = newVal
    }
  }
}
